<template>
    <div class="read-wrapper">
        <div class="btn-groups">
            <a-button @click="backToList" type="primary" shape="round">
                返回文章列表
            </a-button>
        </div>
        <h1 class="title">{{ title }}</h1>
        <v-md-editor v-model="content" mode="preview"></v-md-editor>
        <div class="updateTime">发布于 {{ updateTime }}</div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            title: null,
            content: '',
            updateTime: null,
        }
    },
    methods: {
        // 根据id获取文章
        getArticle() {
            this.$api.article
                .getArticle({ id: this.$store.state.curWritingArticleId })
                .then((res) => {
                    console.log(res.data)
                    this.title = res.data.title
                    this.updateTime = res.data.updateTime
                    this.content = res.data.content
                })
        },
        backToList() {
            this.$router.push({ path: '/home' })
            this.$store.commit('setCurWritingArticleId', null)
        },
    },
    mounted() {
        this.getArticle()
    },
}
</script>

<style lang="scss">
.read-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    .btn-groups {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 20px;
        align-items: center;
    }
    .title {
        text-align: center;
        font-weight: bold;
        font-size: 36px;
    }
    .content {
        margin: 16px 0;
    }
    .updateTime {
        font-size: 14px;
        color: #6c757d;
        margin-top: 16px;
    }
}
</style>
